"use client";

import { useState } from "react";
import { z } from "zod";
import { toast } from "sonner";
import {
    Building2,
    User,
    Mail,
    Phone,
    MessageSquare,
    Package,
    ShoppingBag,
    Send,
    Loader2,
    FileText
} from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import { Label } from "@/components/ui/label";

const bulkOrderSchema = z.object({
    companyName: z.string().min(2, "Company name is required"),
    contactPerson: z.string().min(2, "Contact person name is required"),
    email: z.string().email("Please enter a valid email address"),
    phone: z.string().min(10, "Phone number must be at least 10 digits"),
    gstNumber: z.string().optional(),
    productInterest: z.string().min(1, "Please select a product"),
    quantity: z.string().min(1, "Please enter estimated quantity"),
    message: z.string().optional(),
});

type BulkOrderFormData = z.infer<typeof bulkOrderSchema>;

const productTypes = [
    "Coconut Oil (Agmark)",
    "Sesame Oil (Gingelly Oil)",
    "Groundnut Oil",
    "Sunflower Oil",
    "Deepam Lamp Oil",
    "Castor Oil",
    "Vanaspati",
    "Refined Oil",
    "Baker's Choice",
    "Other / Mixed Order"
];

export default function BulkOrderForm() {
    const [formData, setFormData] = useState<BulkOrderFormData>({
        companyName: "",
        contactPerson: "",
        email: "",
        phone: "",
        gstNumber: "",
        productInterest: "",
        quantity: "",
        message: "",
    });
    const [errors, setErrors] = useState<Partial<BulkOrderFormData>>({});
    const [loading, setLoading] = useState(false);

    const handleInputChange = (field: keyof BulkOrderFormData, value: string) => {
        setFormData((prev) => ({ ...prev, [field]: value }));
        if (errors[field]) {
            setErrors((prev) => ({ ...prev, [field]: undefined }));
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);
        setErrors({});

        const result = bulkOrderSchema.safeParse(formData);

        if (!result.success) {
            const fieldErrors: Partial<BulkOrderFormData> = {};
            result.error.issues.forEach((issue) => {
                if (issue.path[0]) {
                    fieldErrors[issue.path[0] as keyof BulkOrderFormData] = issue.message;
                }
            });
            setErrors(fieldErrors);
            setLoading(false);
            return;
        }

        try {
            const response = await fetch("/api/send-email", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    name: formData.contactPerson,
                    email: formData.email,
                    phone: formData.phone,
                    company: formData.companyName,
                    gst: formData.gstNumber,
                    product: formData.productInterest,
                    quantity: formData.quantity,
                    description: `Bulk Order Inquiry\nCompany: ${formData.companyName}\nProduct: ${formData.productInterest}\nQuantity: ${formData.quantity}\nGST: ${formData.gstNumber || 'N/A'}\n\nMessage: ${formData.message || 'No additional message'}`,
                    type: "bulk_order", // Assuming API can handle type or just content
                }),
            });

            const data = await response.json();

            if (!response.ok) {
                throw new Error(data.error || "Failed to send inquiry");
            }

            toast.success("Bulk order inquiry sent successfully! We will contact you soon.");
            setFormData({
                companyName: "",
                contactPerson: "",
                email: "",
                phone: "",
                gstNumber: "",
                productInterest: "",
                quantity: "",
                message: "",
            });
        } catch (error) {
            console.error("Error submitting bulk order:", error);
            toast.error(
                error instanceof Error
                    ? error.message
                    : "Failed to send inquiry. Please try again."
            );
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="mx-auto max-w-4xl py-8">
            <div className="rounded-2xl border bg-card p-6 md:p-10 shadow-sm">
                <div className="mb-8">
                    <h3 className="text-2xl font-semibold tracking-tight">Request a Quote</h3>
                    <p className="text-muted-foreground mt-2">
                        Fill out the form below with your requirements, and our sales team will get back to you with a competitive quote.
                    </p>
                </div>

                <form onSubmit={handleSubmit} className="space-y-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        {/* Company Details */}
                        <div className="space-y-2">
                            <Label htmlFor="companyName" className="flex items-center gap-2">
                                <Building2 className="h-4 w-4" /> Company / Business Name
                            </Label>
                            <Input
                                id="companyName"
                                placeholder="e.g. Sri Krishna Supermarket"
                                value={formData.companyName}
                                onChange={(e) => handleInputChange("companyName", e.target.value)}
                                className={errors.companyName ? "border-red-500" : ""}
                            />
                            {errors.companyName && (
                                <p className="text-sm text-red-500">{errors.companyName}</p>
                            )}
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="contactPerson" className="flex items-center gap-2">
                                <User className="h-4 w-4" /> Contact Person
                            </Label>
                            <Input
                                id="contactPerson"
                                placeholder="Manager / Owner Name"
                                value={formData.contactPerson}
                                onChange={(e) => handleInputChange("contactPerson", e.target.value)}
                                className={errors.contactPerson ? "border-red-500" : ""}
                            />
                            {errors.contactPerson && (
                                <p className="text-sm text-red-500">{errors.contactPerson}</p>
                            )}
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="email" className="flex items-center gap-2">
                                <Mail className="h-4 w-4" /> Email Address
                            </Label>
                            <Input
                                id="email"
                                type="email"
                                placeholder="purchase@company.com"
                                value={formData.email}
                                onChange={(e) => handleInputChange("email", e.target.value)}
                                className={errors.email ? "border-red-500" : ""}
                            />
                            {errors.email && (
                                <p className="text-sm text-red-500">{errors.email}</p>
                            )}
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="phone" className="flex items-center gap-2">
                                <Phone className="h-4 w-4" /> Phone Number
                            </Label>
                            <Input
                                id="phone"
                                type="tel"
                                placeholder="+91 98765 43210"
                                value={formData.phone}
                                onChange={(e) => handleInputChange("phone", e.target.value)}
                                className={errors.phone ? "border-red-500" : ""}
                            />
                            {errors.phone && (
                                <p className="text-sm text-red-500">{errors.phone}</p>
                            )}
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="gstNumber" className="flex items-center gap-2">
                                <FileText className="h-4 w-4" /> GST Number <span className="text-muted-foreground text-xs font-normal">(Optional)</span>
                            </Label>
                            <Input
                                id="gstNumber"
                                placeholder="GSTIN..."
                                value={formData.gstNumber}
                                onChange={(e) => handleInputChange("gstNumber", e.target.value)}
                            />
                        </div>
                        <div className="space-y-2">
                            <Label htmlFor="productInterest" className="flex items-center gap-2">
                                <ShoppingBag className="h-4 w-4" /> Product Interest
                            </Label>
                            <Select
                                value={formData.productInterest}
                                onValueChange={(value) => handleInputChange("productInterest", value)}
                            >
                                <SelectTrigger className={errors.productInterest ? "border-red-500" : ""}>
                                    <SelectValue placeholder="Select primary product" />
                                </SelectTrigger>
                                <SelectContent>
                                    {productTypes.map((type) => (
                                        <SelectItem key={type} value={type}>
                                            {type}
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            {errors.productInterest && (
                                <p className="text-sm text-red-500">{errors.productInterest}</p>
                            )}
                        </div>
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="quantity" className="flex items-center gap-2">
                            <Package className="h-4 w-4" /> Estimated Quantity
                        </Label>
                        <Input
                            id="quantity"
                            placeholder="e.g. 50 Tins per month / 500 Litres one-time"
                            value={formData.quantity}
                            onChange={(e) => handleInputChange("quantity", e.target.value)}
                            className={errors.quantity ? "border-red-500" : ""}
                        />
                        {errors.quantity && (
                            <p className="text-sm text-red-500">{errors.quantity}</p>
                        )}
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="message" className="flex items-center gap-2">
                            <MessageSquare className="h-4 w-4" /> Additional Requirements
                        </Label>
                        <Textarea
                            id="message"
                            placeholder="Any specific packing requirements, delivery location details, or other questions..."
                            value={formData.message}
                            onChange={(e) => handleInputChange("message", e.target.value)}
                            rows={4}
                        />
                    </div>

                    <Button
                        type="submit"
                        className="w-full bg-[#3c8c1e] hover:bg-[#327a19] text-white py-6 text-lg"
                        disabled={loading}
                    >
                        {loading ? (
                            <>
                                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                                Sending Inquiry...
                            </>
                        ) : (
                            <>
                                <Send className="mr-2 h-5 w-5" />
                                Send Bulk Order Inquiry
                            </>
                        )}
                    </Button>
                </form>
            </div>
        </div>
    );
}
